
<template>

  <div class="div">

  <div class=" grid-container" >
    <div class="grid-item-head item0 grid-container-head">
        <div class="grid-item0a ">{{calcul}}</div>
        <div class="grid-item0b ">{{resultat}}</div>
    </div>

    <div class="grid-item item1" style="background-color: red" @click=reset()>AC</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c1)>/</div>
    <div class="grid-item " style="background-color: rgb(184, 184, 184)" @click=calculer(c2)>x</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c3)>7</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c4)>8</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c5)>9</div>
    <div class="grid-item " style="background-color: rgb(184, 184, 184)" @click=calculer(c6)>-</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c7)>4</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c8)>5</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c9)>6</div>
    <div class="grid-item " style="background-color: rgb(184, 184, 184)" @click=calculer(c10)>+</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c11)>1</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c12)>2</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c13)>3</div>
    <div class="grid-item item15" style="background-color: blue" @click=resultatf(c14)>=</div>
    <div class="grid-item item16" style="background-color: rgb(110, 110, 110)" @click=calculer(c15)>0</div>
    <div class="grid-item " style="background-color: rgb(110, 110, 110)" @click=calculer(c16)>.</div>
  </div>
  <div class="name">
  Design and coded by <br>
  <a href="https://www.w3schools.com/" style="text:white">Octave DESCOURS</a>
  </div>
  </div>

</template>

<script>
export default {
  name: 'HelloWorldBis',
    data() {
        return {
          calcul:"",
          resultat:"0",
          c1:"/",
          c2:"*",
          c3:"7",
          c4:"8",
          c5:"9",
          c6:"-",
          c7:"4",
          c8:"5",
          c9:"6",
          c10:"+",
          c11:"1",
          c12:"2",
          c13:"3",
          c14:"=",
          c15:"0",
          c16:".",
          egal:false,
          nombre:true,
    };
  },
  props: {
    },
  

  methods: {
calculer(valeur){
  if(this.egal==true){
    this.calcul=""
    this.egal=false
  }

  if (this.nombre==true || (this.nombre==false && valeur!=this.c1 && valeur!=this.c2 && valeur!=this.c6 && valeur!=this.c10)){
  
  this.calcul= this.calcul+valeur
  this.resultat=valeur}

  if(valeur==this.c1 || valeur==this.c2 || valeur==this.c6 || valeur==this.c10){
    this.nombre=false}
  else {this.nombre=true;}


  

},
reset(){
  this.calcul= ""
  this.resultat="0"
},
resultatf(valeur){

  var c = eval(this.calcul)
  this.resultat=c.toString()
  this.calcul= this.calcul+valeur+c.toString()
  this.egal=true
}
  }
}

</script>

